import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { ToastController, LoadingController, AlertController } from '@ionic/angular';
import { AccessProviders } from '../../providers/access-providers';
import { HttpClient } from '@angular/common/http';
import { from, Observable, Subscriber } from 'rxjs';

import { registroPSL } from '../modelo_registroPSL/registroPSL';


//dato del calculo
import { datoL } from '../modelo_datoL/datoL';

@Component({
  selector: 'app-registro-pedi-semanal-ll',
  templateUrl: './registro-pedi-semanal-ll.page.html',
  styleUrls: ['./registro-pedi-semanal-ll.page.scss'],
})
export class RegistroPediSemanalLlPage implements OnInit {

  //dato de calculo
  objeto12:datoL = { semana:"", total_costo:""}
  
  pruebas12: datoL[] = [];
  url3 = 'http://localhost/proG/server_api/menu_datoL.php';



  objeto:registroPSL = { Des_de_semana:"", mes:"", lunes:"", martes:"", miercoles:"", jueves:"", viernes:"", sabado:"", domingo:""}
  
  pruebas: registroPSL[] = [];
  url = 'http://localhost/proG/server_api/crear_registroPSL.php';
  url2 = 'http://localhost/proG/server_api/menu_registroPSL.php';
 
  disabledButton;


  constructor(
    public http: HttpClient,
    private router: Router,
    private toastCtrl: ToastController,
    private loadingCtrl: LoadingController,
    private alertCtrl: AlertController,
    private accsPrvds: AccessProviders
  ) { 
    this.listarRegistroPSL();
    this.listarRegistroDL();
  }

  ngOnInit() {
  }

  agregarRegistroPSL(obj :registroPSL): Observable<registroPSL>{
    return this.http.post<registroPSL>(this.url, JSON.stringify(obj))
  }

  async agregarSE() {
    this.agregarRegistroPSL(this.objeto)
    .subscribe((resp) =>{
      this.objeto.Des_de_semana='';
      this.objeto.mes='';
      this.objeto.lunes='';
      this.objeto.martes='';
      this.objeto.miercoles='';
      this.objeto.jueves='';
      this.objeto.viernes='';
      this.objeto.sabado='';
      this.objeto.domingo='';
      this.listarRegistroPSL();
      console.log(resp)
    })
    const toast = await this.toastCtrl.create({
      message: "Registro Exitoso",
      duration: 1500
    });
    toast.present();
  }

  getRegistroPSL(): Observable<any>{
    return this.http.get(this.url2);
  }

  listarRegistroPSL(){
    this.pruebas = [];
    this.getRegistroPSL().subscribe(res => {
      for(let i=0; i<res.length; i++){
        this.pruebas.push(res[i]);
      }
    })
  }


  
  async delData(a){
    return new Promise(resolve => {
      let body = {
        aksi: 'eliminar_PSL',
        id: a
      }
      this.accsPrvds.postData(body, 'proses_api.php').subscribe((res:any)=>{
        if(res.success==true){
          this.presentToast('Eliminado con exito');
          this.listarRegistroPSL();
        }else{
          this.presentToast('delete error');
        }
      });

    }); 

  }

  async presentToast(a){
    const toast = await this.toastCtrl.create({
      message: a,
      duration: 1500
    });
    toast.present();
  }

  abrirCrud(a){
    this.router.navigate(['/modsemanall/'+a]);
  }

   /// datos visualizzar 
   getRegistroDL(): Observable<any>{
    return this.http.get(this.url3);
  }

  listarRegistroDL(){
    this.pruebas12 = [];
    this.getRegistroDL().subscribe(res => {
      for(let i=0; i<res.length; i++){
        this.pruebas12.push(res[i]);
      }
    })
  }
  
  async delDatos(a){
    return new Promise(resolve => {
      let body = {
        aksi: 'eliminar_DL',
        id: a
      }
      this.accsPrvds.postData(body, 'proses_api.php').subscribe((res:any)=>{
        if(res.success==true){
          this.presentToast('Eliminado con exito');
          this.listarRegistroDL();
        }else{
          this.presentToast('delete error');
        }
      });

    }); 

  }

}
